/* global console, document, Excel, Office */
import * as _ from "lodash";
Office.onReady(info => {
  if (info.host === Office.HostType.Excel) {
    document.getElementById("loadContainer").onclick = loadContainers;
    document.getElementById("loadPalletes").onclick = loadPallettes;
    document.getElementById("runAllocatorBtn").onclick = runAllocation;
    document.getElementById("stopAllocatorBtn").onclick = stopAllocation;
  }
});
var containers: Container[] = [];
var pallettes: Container[] = [];
var shouldStopAllocation = false;

function getNodeValue(id: string) {
  const d: any = document.getElementById(id);
  return d.value;
}
export async function loadContainers() {
  try {
    await Excel.run(async context => {
      const values = await getRangeValues(context);
      containers = _.flatMap(
        _.slice(values, 1).map(value => {
          // index
          // 0: name, 1: weight, 2: length, 3: width, 4: height, 5: inside length, 6: inside width, 7: inside height
          const outsideContainer: Container = {
            name: value[0],
            type: "outside",
            kind: "container",
            dimensions: {
              length: {
                metric: "mm",
                value: value[2]
              },
              width: {
                metric: "mm",
                value: value[3]
              },
              height: {
                metric: "mm",
                value: value[4]
              },
              weight: {
                metric: "kg",
                value: value[1]
              }
            }
          };
          const insideContainer: Container = {
            name: value[0],
            type: "inside",
            kind: "container",
            dimensions: {
              length: {
                metric: "mm",
                value: value[5]
              },
              width: {
                metric: "mm",
                value: value[6]
              },
              height: {
                metric: "mm",
                value: value[7]
              },
              weight: {
                metric: "kg",
                value: value[1]
              }
            }
          };
          return [outsideContainer, insideContainer];
        }),
        item => _.map(item)
      );
    });
  } catch (error) {
    console.error(error);
    throw error;
  }
}

export async function loadPallettes() {
  try {
    await Excel.run(async context => {
      const values = await getRangeValues(context);
      pallettes = _.slice(values, 1).map(value => {
        // index
        // 0: name, 1: length, 2: width, 3: height, 4: volume
        const pallette: Container = {
          name: value[0],
          type: "outside",
          kind: "pallett",
          dimensions: {
            length: {
              metric: "cm",
              value: value[1]
            },
            width: {
              metric: "cm",
              value: value[2]
            },
            height: {
              metric: "cm",
              value: value[3]
            },
            volume: {
              metric: "ccm",
              value: value[4]
            }
          }
        };

        return pallette;
      });
    });
  } catch (error) {
    console.error(error);
    throw error;
  }
}

async function getRangeValues(context: Excel.RequestContext) {
  const ranges = await getSelectedRangeAddress(context);
  return getRangeValuesCore(context, ranges.address);
}

async function getSelectedRangeAddress(context: Excel.RequestContext) {
  const ranges = context.workbook.getSelectedRanges();
  ranges.load("address");
  await context.sync();
  return ranges;
}

async function getRangeValuesCore(context: Excel.RequestContext, address: string) {
  const sheet = context.workbook.worksheets.getItem("Sheet1");
  var range = sheet.getRange(address);
  range.load("values");
  await context.sync();
  return range.values;
}

async function stopAllocation(){
  shouldStopAllocation = true;
}
async function runAllocation() {
  console.log(containers);
  console.log(pallettes);
  const mergedContainers = [...containers, ...pallettes];
  shouldStopAllocation = false;

  const rowStartIndex = +getNodeValue("rowStartIndex");
  const rowEndIndex = +getNodeValue("rowEndIndex");
  const startColumn = getNodeValue("columnStartCell");
  const endColumn = getNodeValue("columnEndCell");
  const resultColumnCellPacked = getNodeValue("columnAllocationResultForPacked");
  const resultColumnCellUnPacked = getNodeValue("columnAllocationResultForUnPacked");

  try {
    await Excel.run(async context => {
      for (let index = rowStartIndex; index <= rowEndIndex; index++) {
        await handleArticleRow(
          context,
          index,
          startColumn,
          endColumn,
          resultColumnCellPacked,
          resultColumnCellUnPacked,
          mergedContainers
        );
        if(shouldStopAllocation){
          console.log("Allocation Stopped by user");
          break;
        }
      }
      console.log("Completed");
    });
  } catch (error) {
    console.error(error);
    throw error;
  }
}

async function handleArticleRow(
  context: Excel.RequestContext,
  index: number,
  startColumn: string,
  endColumn: string,
  resultColumnCellPacked: string,
  resultColumnCellUnPacked: string,
  mergedContainers: Container[]
) {
  // 1. Create Article row
  const articles = await createArticleRow(context, index, startColumn, endColumn);

  // 2. Allocate
  const packedAllocatedContainers = getAllocatedContainer(articles[0], mergedContainers);
  const unPackedAllocatedContainers = getAllocatedContainer(articles[1], mergedContainers);

  // 3. Put into cell
  await addAllocationResultToCell(context, packedAllocatedContainers, articles[0], resultColumnCellPacked);
  await addAllocationResultToCell(context, unPackedAllocatedContainers, articles[1], resultColumnCellUnPacked);
}

async function createArticleRow(context: Excel.RequestContext, index: number, startColumn: string, endColumn: string) {
  // read the start row index, column start and end
  // work on the assumptions:
  //    Article packed: 5 - length, 6 - width, 7 - height  (all mm)
  //    Article unpacked : 10 - Length, 11 - width, 12 - height (all mm)
  //    Weight for both: 8 - width (kg)
  const address = `${startColumn}${index}:${endColumn}${index}`; //"3A:3M"
  const values = _.flatMap(await getRangeValuesCore(context, address), value => _.map(value));
  const packedArticle: Article = {
    id: values[2],
    index,
    type: "packed",
    dimensions: {
      length: {
        metric: "mm",
        value: values[5]
      },
      width: {
        metric: "mm",
        value: values[6]
      },
      height: {
        metric: "mm",
        value: values[7]
      },
      weight: {
        metric: "kg",
        value: values[8]
      }
    }
  };
  const unpackedArticle: Article = {
    id: values[2],
    index,
    type: "unpacked",
    dimensions: {
      length: {
        metric: "mm",
        value: values[10]
      },
      width: {
        metric: "mm",
        value: values[11]
      },
      height: {
        metric: "mm",
        value: values[12]
      },
      weight: {
        metric: "kg",
        value: values[8]
      }
    }
  };
  return [packedArticle, unpackedArticle];
}

function getAllocatedContainer(article: Article, containers: Container[]) {
  const possibleAllocatedContainers: Container[] = [];
  for (let index = 0; index < containers.length; index++) {
    const element = containers[index];
    // dont consider weight if container does not have it
    const containerWeight = element.dimensions.weight;
    if (
      article.dimensions.length.value < getDimensionValue(element.dimensions.length) &&
      article.dimensions.height.value < getDimensionValue(element.dimensions.height) &&
      article.dimensions.width.value < getDimensionValue(element.dimensions.width) &&
      (containerWeight !== undefined ? article.dimensions.weight.value < containerWeight.value : true)
    ) {
      possibleAllocatedContainers.push(element);
    }
  }
  return possibleAllocatedContainers;
}

async function addAllocationResultToCell(
  context: Excel.RequestContext,
  containers: Container[],
  article: Article,
  resultColumnCell: string
) {
  const containerValue = containers.map(container => container.name).join(" , ");
  const cellAddress = `${resultColumnCell}${article.index}`;

  const cellValue = `Article ${article.type} -> ${containerValue}`;

  await setCellValue(context, cellAddress, cellValue);
}

function getDimensionValue(dimension: Dimension) {
  return dimension.metric === "mm" ? dimension.value : dimension.value * 10;
}

async function setCellValue(context: Excel.RequestContext, cellAddress: string, containerValue: string) {
  var sheet = context.workbook.worksheets.getItem("Sheet1");

  var range = sheet.getRange(cellAddress);

  range.values = [[containerValue]];
  range.format.autofitColumns();

  await context.sync();
}
type Metric = "mm" | "cm" | "kg" | "ccm";
interface Dimension {
  value: number;
  metric: Metric;
}
interface Dimensions {
  height: Dimension;
  width: Dimension;
  length: Dimension;
  weight?: Dimension;
  volume?: Dimension;
}
type Kind = "container" | "pallett";

type ContainerType = "outside" | "inside";

interface Container {
  type: ContainerType;
  name: string;
  dimensions: Dimensions;
  kind: Kind;
}

type ArticleType = "packed" | "unpacked";
interface Article {
  index?: number;
  id?: string;
  type: ArticleType;
  dimensions: Dimensions;
}

interface AllocationResult {
  article: Article;
  container?: Container;
}
